<script>
import axios from 'axios';

export default {
  data() {
    return {
      dataDocs: null
    }
  },
  async mounted () {
    await this.fetchDocs()
  },
  methods: {
    fetchDocs() {
      const axios = require('axios')
      const options = {
        headers: {
          'Accept': 'application/vnd.api+json',
          'Content-Type': 'application/vnd.api+json',
          
        }
      };
      const getBreeds = async () => {
        try {
          return await axios.get('http://www.tlu.edu.vn/DesktopModules/cmsview/api/Document/list?DocNumber=&ImplementId=0&IssueId=0&Keyword=&PortalId=0&PriorityId=0&SectorId=0&Status=4&TypeId=18&pageindex=1&pagesize=10', options)
        } catch (error) {
          console.error(error)
        }
      }

      const countBreeds = async () => {
        const breeds = await getBreeds()

        if (breeds.data.message) {
          console.log(`Got ${Object.entries(breeds.data.message).length} breeds`)
        }
      }

      countBreeds()
    }
  }
};
</script>
